html {
  background: url(img/tileable_wood_texture.png);
}

header {
  margin: auto;
  text-align: center;
}

h1 {
  font-family: 'Hanalei Fill', cursive;
  font-size: 100px;
  color: #009ce5;
  text-shadow: 0 8px 9px #fb0cb2, 0px -2px 1px #8e246d;
  letter-spacing: 6px;
}

h2 {
  font-family: 'Montserrat', sans-serif;
  font-size: 40px;
  color: #009ce5;
}

p {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 22px;
}

.fotos {
  display: flex;
  justify-content: center;
  max-width: 1080px;
  margin: auto;
}

.fotos img {
  max-width: 100%;
}

.contenedorGrid {
  padding-top: 50px;
  display: grid;
  grid-gap: 8px;
  grid-template-columns: minmax(160px, 1fr) minmax(160px, 1fr) minmax(160px, 1fr);
  grid-template-rows: minmax(245px, auto) minmax(245px, auto) minmax(245px, auto);
  align-items: center;
  justify-content: center;
  height: 100%;
}

.contenedor-basico {
  width: 98%;
  max-width: 1080px;
  margin: auto;
}

.contenedorGrid .impar {
  grid-column-start: 1;
  grid-column-end: 2;
}

.contenedorGrid .par {
  grid-column-start: 2;
  grid-column-end: 3;
}

.blanco-negro-comun {
  display: flex;
  align-items: center;
  height: 100%;
}

.negro {
  justify-content: center;
  background-color: black;
  font-weight: lighter;
}

.negro p {
  font-size: 32px;
  text-align: center;
}

.blanco {
  background-color: white;
  padding-left: 8px;
  padding-right: 8px;
}

.naranja {
  background: #ff5e01;
  color: #002f4a;
}

.azul {
  background: #002f4a;
  color: #ff5e01;
}

.lima {
  background: #00ab7f;
  color: #ab002c;
}

.fucsia {
  background: #ab002c;
  color: #00ab7f;
}

.verde {
  background: #7a7b00;
  color: #01007b;
}

.vino {
  background: #01007b;
  color: #60a100;
}

.verdoso {
  background: #007f0a;
  color: #7f0075;
}

.morado {
  background: #7f0075;
  color: #007f0a;
}

.black {
  color: #fff;
}
.boton {
  background: #002f4a;
  color: #ff5e01;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 84px;
  margin-top: 10px;
}
.boton p {
  font-size: 2em;
  transition: 1s;
}
.boton p:hover {
  transform: scale(1.5);
}

.boton i {
  padding: 0 4px;
}

footer {
  background: #303030;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2px 14px;
}

footer p {
  font-size: .8em;
}

.redes {
  display: flex;
  align-items: center;
}

.redes i {
  padding-left: 20px;
  font-size: 2em;
}

@media (max-width: 840px) {
  .titulos {
    top: 22%;
  }

  .contenedorGrid {
    grid-template-columns: minmax(160px, 1fr) minmax(160px, 1fr);
    grid-template-rows: minmax(245px, auto) minmax(245px, auto);
  }

  .contenedorGrid .par {
    grid-column-start: 1;
    grid-column-end: 2;
  }
}

@media (max-width: 600px) {
  .titulos {
    top: 18%;
  }

  .contenedorGrid {
    grid-template-columns: 1fr;
  }

  h1 {
    font-size: 60px;
  }

  h2 {
    font-size: 26px;
  }

  footer {
    flex-direction: column;
  }
}

@media (max-width: 400px) {
  .boton p {
    font-size: 1.4em;
  }
}